@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Press+Start+2P&display=swap');
@import "./App_mobile.css";
:root {
  --primary-color:rgb(238,21,21)  ;
  --secundary-color:rgb(240,240,240);
}
* {
  font-family: 'Press Start 2P', cursive;
}

body {
  background: var(--primary-color);
  border-radius: 50%;
}

h1 {
  font-weight: normal;
  font-size: 40px;
}
p {
  font-weight: bold;
  font-size: 40px;
}
li {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.2s;
}

li:hover {
  background-color: var(--primary-color);
  color: var(--secundary-color);
}
li p {
  font-size: 20px;
}
img {
  width: 20%;
}

div.container {
  text-align: center;
  padding: 50px;
  margin: 0 auto;
  border: 3px solid black;
  background-color: var(--secundary-color);
  color: black;
  width: 100%;
  border-bottom: 5px solid black;
  box-sizing: border-box;
  
}

input {
  outline: none;
  padding: 10px 10px;
  background-color: var(--secundary-color);/*  */
  border: 2px solid var(--primary-color);
  width: 65%;
  font-size: 19px;
  margin: 15px auto 0px auto;
}

input:hover {
  box-shadow: 0px 0px 1px 1px rgba(220, 20, 60, 0.8);
}
.lista-busquedas{
  width: 65%;
  height: 300px;
  margin: auto;
  overflow-y: scroll;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;

}

span img {
  width: 50px;
  height: 50px;
}

.pokedex img {
  width: 80px;
  height: 80px;
}



