@media (max-width:1000px) {
    div.container {
      width: 100%;
      border: none;
      margin: 0;
      border-radius: 0;
      background-color: rgb(255, 255, 255);
    }
    h1{
        font-size: 30px;
    }
  
    img {
        width: 60%;
      }
    .container-loader p {
      font-size: 25px;
      text-align: center;
    }
  
    .lista-busquedas {
      height: 250px;
      overflow-y: scroll;
      scrollbar-width: 2px;
      width: 95%;
    }
  
    li p {
  
      font-size: 14px;
    }
  
    p {
      font-size: 14px;
    }
  
    input {
      width: 95%;
      font-size: 13.5px;
      padding: 10px 0px;
    }
  
  
    p {
      font-size: 20px;
    }
  }