@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width:1000px) {
    div.container {
      width: 100%;
      border: none;
      margin: 0;
      border-radius: 0;
      background-color: rgb(255, 255, 255);
    }
    h1{
        font-size: 30px;
    }
  
    img {
        width: 60%;
      }
    .container-loader p {
      font-size: 25px;
      text-align: center;
    }
  
    .lista-busquedas {
      height: 250px;
      overflow-y: scroll;
      scrollbar-width: 2px;
      width: 95%;
    }
  
    li p {
  
      font-size: 14px;
    }
  
    p {
      font-size: 14px;
    }
  
    input {
      width: 95%;
      font-size: 13.5px;
      padding: 10px 0px;
    }
  
  
    p {
      font-size: 20px;
    }
  }
:root {
  --primary-color:rgb(238,21,21)  ;
  --secundary-color:rgb(240,240,240);
}
* {
  font-family: 'Press Start 2P', cursive;
}

body {
  background: rgb(238,21,21);
  background: var(--primary-color);
  border-radius: 50%;
}

h1 {
  font-weight: normal;
  font-size: 40px;
}
p {
  font-weight: bold;
  font-size: 40px;
}
li {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.2s;
}

li:hover {
  background-color: rgb(238,21,21);
  background-color: var(--primary-color);
  color: rgb(240,240,240);
  color: var(--secundary-color);
}
li p {
  font-size: 20px;
}
img {
  width: 20%;
}

div.container {
  text-align: center;
  padding: 50px;
  margin: 0 auto;
  border: 3px solid black;
  background-color: rgb(240,240,240);
  background-color: var(--secundary-color);
  color: black;
  width: 100%;
  border-bottom: 5px solid black;
  box-sizing: border-box;
  
}

input {
  outline: none;
  padding: 10px 10px;
  background-color: rgb(240,240,240);
  background-color: var(--secundary-color);/*  */
  border: 2px solid rgb(238,21,21);
  border: 2px solid var(--primary-color);
  width: 65%;
  font-size: 19px;
  margin: 15px auto 0px auto;
}

input:hover {
  box-shadow: 0px 0px 1px 1px rgba(220, 20, 60, 0.8);
}
.lista-busquedas{
  width: 65%;
  height: 300px;
  margin: auto;
  overflow-y: scroll;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;

}

span img {
  width: 50px;
  height: 50px;
}

.pokedex img {
  width: 80px;
  height: 80px;
}




